import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import './map.css';

const { kakao } = window;

const ResultContainer = styled.div`
  width: 304px;
  height: 208px;
  border-radius: 10px;
`;

export default function MapPost({ place }) {
  const [lat, lng] = [place.y, place.x];

  useEffect(() => {
    const mapContainer = document.getElementById('map');
    const mapOption = {
      center: new kakao.maps.LatLng(lat, lng),
      level: 3, //  
    };

    //  
    const map = new kakao.maps.Map(mapContainer, mapOption);

    //   
    var markerPosition = new kakao.maps.LatLng(lat, lng);

    //  
    var marker = new kakao.maps.Marker({
      position: markerPosition,
    });

    //     
    marker.setMap(map);

    // 
    var iwContent = `<div style="padding:5px;">${place.place_name} <a href="https://map.kakao.com/link/to/${place.place_name},${lat},${lng}" style="color:blue" target="_blank"></a></div>`, //    HTML  document element 
      iwPosition = new kakao.maps.LatLng(lat, lng); //  

    //  
    var infowindow = new kakao.maps.InfoWindow({
      position: iwPosition,
      content: iwContent,
    });

    //    .   marker     
    infowindow.open(map, marker);
  }, [place]);

  return (
    <ResultContainer>
      <div id="map" className="map"></div>
    </ResultContainer>
  );
}
